.orders-item {
    display: flex;
    flex-direction: column;
    gap: 24px;
    border-radius: 40px;
    background: #1c1c21;
    /* max-width: 530px; */
    min-height: 48px;
    font-family: 'Jet Brains Mono';
    font-size: 16px;
    line-height: 24px;
    color: #f2f2f3;
    width: 100%;
}

.orders-info {
    display: flex;
    justify-content: space-between;
}

.icon {
    width: 64px;
    height: 64px;
    border: double 2px transparent;
    background-image: linear-gradient(rgba(19, 19, 22, 1), rgba(19, 19, 22, 1)),
    radial-gradient(#801ab2, #4c4cff);
    border-image-slice: 20;
    border-radius: 50%;
    background-clip: content-box, border-box;
    display: flex;
    align-items: center;
    justify-content: center;
}

.image {
    width: 170%;
    height: 100%;
}

.list {
    list-style-type: none;
    padding-left: 0;
    display: flex;
    position: relative;
}

.list-item {
    margin-right: -20px;
    position: relative;
}

.overlay {
    background: rgba(19, 19, 22, 0.6);
    width: 62px;
    height: 62px;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    position: absolute;
    top: 2px;
    right: -15px;
}

.status_color_yellow {
    color: #deb110;
}

.status_color_white {
    color: #f2f2f3;
}

.status_color_green {
    color: #00cccc;
}
