.main {
    width: 100%;
    height: 100%;
    box-sizing: border-box;
    max-width: 1320px;
    margin-left: auto;
    margin-right: auto;
    display: flex;
    gap: 60px;
}

::-webkit-scrollbar {
    width: 12px;
    height: 300px;
}
::-webkit-scrollbar-track {
    background: #2f2f37;
    height: 300px;
}
::-webkit-scrollbar-thumb {
    background: #8585ad;
    border: 2px solid #2f2f37;
    border-radius: 6px;
}

.container {
    display: flex;
    align-self: flex-start;
    justify-content: center;
    flex-grow: 1;
    flex-direction: column;
    text-align: center;
}
